  documentComment:string = "";

  isFileAdded:boolean = false;
  selectedFile: File;
  fileList: File[] = [];
  listOfFiles: any[] = [];
  uploadResponse:any;
  error:string = "";
  isFileDetailsAvailable:boolean = false;
  fileUrl:string = "";
  filePath:any;
  reqUserId: any;
  uploadedDocumentList:any;
  getUserRole: any;
  min = new Date();
  showDateError: boolean = false;
  fileData;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private sanitizer: DomSanitizer,
    private moveInOutService:MoveInOutService,
  	private userService:UserService,
    private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
    private fileUploadService: FileUploadService,
    private fileDownloadService: FileDownloadService,
    private fileDetailsService: FileDetailsService, 
  ) { }


  getBlock() {

    let params = {
      apartmentBlockUnitId: parseInt(this.apartmentBlockUnitId)
    }

    this.apartmentService.getApartmentBlockUnitById(params).subscribe((res:any) => {
        this.apartmentBlockNumber = res[0].apartmentBlockNumber
        this.apartmentBlockUnitNumber = res[0].apartmentBlockUnitNumber
    },
    error => {
      console.log(error);
    }
    );
  }

  isUploadProgess(){
    return this.uploadResponse.status == "progress" ? true : false;
  }
  
  isUploadCompleted(){
    return this.uploadResponse.status == "completed" ? true : false;
  }

  uploadFile(event){

    this.fileData = event[0];
    let userId = parseInt(this.cookieService.get('userId'))

    this.fileUploadService.upload(this.fileData, userId).subscribe((res:any) => {

      if(res != undefined){
        this.uploadResponse = res;
      }

      if(this.isUploadCompleted()){

          let newParams = {
          fileDetailsId: this.uploadResponse.fileId, 
          apartmentId: Number(this.cookieService.get('apartmentId'))
        }

        this.fileDetailsService.getFileDetailsById(newParams).subscribe((res:any) => {
          this.filePath = res[0].filePath;
          this.downloadFile(this.filePath)
        })

      }

    })

  }

  downloadFile(filePath) {

    this.fileDownloadService.downloadFile(filePath).subscribe((res:any) => {

      this.isFileDetailsAvailable = true;

      let splitFile = filePath.split('.')
      const blob = res.body
      let objectURL = URL.createObjectURL(blob); 
      let sanitizeUrl:any = this.sanitizer.bypassSecurityTrustUrl(objectURL);
      this.fileUrl = sanitizeUrl
      
    })

  }

  deleteFile(){


    let details = {
      "fileDetailsId": this.uploadResponse.fileId,
      "fileName": this.filePath,
      "filePath": this.filePath,
      "fileSize": 0,
      "description": this.filePath,
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": new Date().toISOString(),
      "updatedBy": null,
      "updatedOn": null,
      "apartmentId": parseInt(this.cookieService.get('apartmentId'))
    }

    let params = {
      fileDetails: details
    }

    this.fileDetailsService.deleteFileDetails(params).subscribe((res:any) => {
      this.isFileDetailsAvailable = false;
      this.uploadResponse = { status: '', message: '', fileId: null };
    })

  }


  // uploadFile(event){
  //   this.isFileAdded = true;

  //   for (var i = 0; i <= event.target.files.length - 1; i++) {
  //     var selectedFile = event.target.files[i];
  //     this.fileList.push(selectedFile);
  //     this.listOfFiles.push({'name':selectedFile.name, 'size':selectedFile.size});

  //     console.log(this.fileList[0]);
