import { Component, OnInit, Injector, ViewChild, HostListener } from '@angular/core';


import { MoveInOutService } from 'src/app/api/controllers/MoveInOut';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { ModalService } from 'src/app/shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';
import { Router, ActivatedRoute } from '@angular/router';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';

@Component({
  selector: 'app-movein-admin-history',
  templateUrl: './movein-admin-history.component.html',
  styleUrls: ['./movein-admin-history.component.scss']
})
export class MoveinAdminHistoryComponent implements OnInit {

  isDataLoaded: boolean = false;

  moveInOutHitoryData: string = "";
  moveInDataList: any;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 10;

  unitFieldType: string = "unitno";
  unitOrder: boolean = false;

  isMobile: boolean = false;

  apartmentBlockId: any;
  apartmentBlockUnitId: any;

  blockUnitData: any;

  modalService: any;
  isError: boolean = false;
  alertMessage: string = "";
  selectedInput: string = "";
  columnField: any = {};
  statusNames: any;
  statusType: any = {};
  listData: any;
  columnData:any;
  gridSourceData:any;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
    private injector: Injector,
    private moveInOutService: MoveInOutService,
    private apartmentService: ApartmentService,
    private lookupService: LookupService,
    private cookieService: CookieService
  ) {
    this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event) {
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type) {
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if (this.unitFieldType == type) {
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value) {
    this.selectedInput = value;
  }

  onDateChange(event, type) {

    if (event != null) {
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("DD/MM/YYYY");
    }
    else {
      this.columnField = {};
    }

  }

  getDate(movein, date) {

    this.moveInDataList.map(obj => {
      if (obj.id == movein.id) {
        obj.inDateData = moment(date).format("DD/MM/YYYY");
      }
    });

    return moment(date).format("DD/MM/YYYY");
  }

  getTime(time) {
    var timeData = moment(time, 'HH:mm:ss').toDate();
    return moment(timeData).format('HH:mm A');
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable() {
    return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable() {
    return this.totalItems == 0 ? true : false;
  }


  getBlockNo(item, id) {
    var data = _.filter(this.blockUnitData, item => {
      if (item.apartmentBlockUnitId === id)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {

      this.moveInDataList.map(obj => {
        if (obj.id == item.id) {
          obj.apartmentBlockNumber = data[0].apartmentBlockNumber;
        }
      });

      return data[0].apartmentBlockNumber;
    }
  }

  getBlockUnitNo(item, id) {
    var data = _.filter(this.blockUnitData, item => {
      if (item.apartmentBlockUnitId === id)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {

      this.moveInDataList.map(obj => {
        if (obj.id == item.id) {
          obj.apartmentBlockUnitNumber = data[0].apartmentBlockUnitNumber;
        }
      });

      return data[0].apartmentBlockUnitNumber;
    }
  }

  
  onGlSearchFilter() {

    if(this.moveInOutHitoryData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.moveInOutHitoryData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }
  getPrintParams(event){
    this.datagrid.exportdata(event, 'moveinHistoryData');
  }
  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
         text: 'Id',
         datafield: 'serialNo',
         width: 100,
         pinned: true,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
      }, {
          text: 'Name',
          datafield: 'userName',
          width: 120,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
      },{
         text: 'Unit Details',
         datafield: 'block_Unit',
         cellsrenderer: cellsrenderer,
         width: 120,
         renderer: columnrenderer
     }, {
         text: 'Phone',
         datafield: 'mobile',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Email',
         datafield: 'email',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
      text: 'Request Type',
      datafield: 'requestType',
      width: 100,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
  },{
         text: 'Date',
         datafield: 'date',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD/MM/YYYY") +'</div>'
        },
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Time',
         datafield: 'time',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ this.getTime(value) +'</div>'
        },
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Status',
         datafield: 'statusId',
         cellsalign: 'center', 
         align: 'center',
         width: 120,
         cellsrenderer: (row, column, value): string => {
           console.log('Hi', value)
           if(value == 374) { return '<div class="dots-mr"><span class="dots m-yellow"></span></div>'}
           else if(value == 380) { return '<div class="dots-mr"><span class="dots grey-850"></span></div>'}
           else if(value == 375) { return'<div class="icon-wrapper dots-mr"><img src="assets/images/movein-icon.svg" class="svg" width="17" height="17" alt="Moved In"></div>'}
           else if(value == 376) { return'<div class="icon-wrapper dots-mr"><img src="assets/images/moveout-icon.svg" class="svg" width="17" height="17" alt="Moved In"></div>'}
        },
         renderer: columnrenderer
     }];


    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      statusIds: "374,375,380"
    }

    this.moveInOutService.getMoveInByStatusIds(params).subscribe((res: any) => {

      let data1 = res.filter(item => {
        item.time = item.inTime;
        item.date = item.inDate;
        item.outTime = '';
        item.outDate = '';
        item.noDue = '';
        return item.isActive;
      });
      let params = {
        apartmentId: parseInt(this.cookieService.get('apartmentId')),
        statusIds: "374,376,380"
      }
      this.moveInOutService.getMoveOutByStatusIds(params).subscribe((res: any) => {

        let data2 = res.filter(item => {
          item.time = item.outTime;
          item.date = item.outDate;
          item.inDate = '';
          item.inTime = '';
          return item.isActive;
        });
      this.moveInDataList = data1.concat(data2);
      console.log('this.moveInDataList', this.moveInDataList)

      this.gridSourceData = {
        localdata: this.moveInDataList,
        datatype: "array"
      }
  
      this.listData = new jqx.dataAdapter(this.gridSourceData);
      this.totalItems = this.moveInDataList.length;

      

      this.isDataLoaded = true;
    })
    });

    this.apartmentService.getAllApartmentBlockUnits().subscribe((res: any) => {

      this.blockUnitData = res;

    });
    this.getStatusName();
  }

  getStatusName() {

    let params = {
      ApartmentId: parseInt(this.cookieService.get('apartmentId')),
      LookupTypeId: 92
    }

    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res: any) => {

      this.statusNames = res.filter(item => {
        return item.isActive;
      });
      this.statusNames.forEach(element => {
        this.statusType[element.lookupValueId] = element;
      });
    });
  }
}
