import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { MoveInOutService } from 'src/app/api/controllers/MoveInOut';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import * as _ from 'underscore';
import * as moment from 'moment';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Router } from '@angular/router';
import { ConfirmDialogModel, CommonConfirmModalComponent } from 'src/app/shared/components/common-confirm-modal/common-confirm-modal.component';
import { MatDialog } from '@angular/material/dialog';
import { MoveOutDetailEditComponent } from 'src/app/shared/components/moveout-detail-edit/moveout-detail-edit.component';

@Component({
  selector: 'app-moveout-maintain',
  templateUrl: './moveout-maintain.component.html',
  styleUrls: ['./moveout-maintain.component.scss']
})
export class MoveoutMaintainComponent implements OnInit {

  isDataLoaded:boolean = false;

  moveOutData:string = "";
  moveOutDataList:any;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  isMobile:boolean = false;

  apartmentBlockId:any;
  apartmentBlockUnitId:any;

  blockUnitData:any;

  isMoveOutSubmitted:boolean = true;

  isError:boolean = false;
  alertMessage:string = "";

  selectedInput:string = "";
  columnField: any = {};
  listData: any;
  columnData:any;
  gridSourceData:any;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
    private router: Router,
    private moveInOutService:MoveInOutService,
    private apartmentService:ApartmentService,
    private sharedService:SharedService,
    private cookieService:CookieService,
    private dialog: MatDialog
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onDateChange(event, type){

    if(event != null){
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("DD/MM/YYYY");
    }
    else {
      this.columnField = {};
    }
    
  }

  getDate(movein, date){

    this.moveOutDataList.map(obj => {
      if(obj.id == movein.id){
        obj.outDateData = moment(date).format("DD/MM/YYYY");
      }
   });

    return moment(date).format("DD/MM/YYYY"); 
  }

  getTime(time){
    var timeData = moment(time, 'HH:mm:ss').toDate();
    return moment(timeData).format('HH:mm A');
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable(){
    return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }


  getBlockNo(item, id){
    var data = _.filter( this.blockUnitData, item => {
      if(item.apartmentBlockUnitId === id)
      return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {

      this.moveOutDataList.map(obj=> {
        if(obj.id == item.id){
          obj.apartmentBlockNumber = data[0].apartmentBlockNumber;
        }
      });

      return data[0].apartmentBlockNumber;
    }
  }

  getBlockUnitNo(item, id){
    var data = _.filter( this.blockUnitData, item => {
      if(item.apartmentBlockUnitId === id)
      return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {

      this.moveOutDataList.map(obj=> {
        if(obj.id == item.id){
          obj.apartmentBlockUnitNumber = data[0].apartmentBlockUnitNumber;
        }
      });

      return data[0].apartmentBlockUnitNumber;
    }
  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'moveOutMaintainData');
  }
 
  navigateToMoveOut() {
    this.router.navigate(['ams/moveinout-tracker/create-moveout']);
  }

  getApproveStatus(data){
    if(data == null)
      return 'high'
    else
      return 'low'
  }

  @HostListener('window:showMoveOutDetails', ['$event.detail'])
  showMoveOutDetails(detail): void {
    let details = this.datagrid.getrowdata(detail.rowId);
    let type_ =  detail.column;
     const dialogRef = this.dialog.open(MoveOutDetailEditComponent, {
        width: '400px',
        data: {
          data: details,
          type: type_
        }
      });
  
      dialogRef.afterClosed().subscribe(result => {
        
        if(result){
        this.isDataLoaded = false;
        let params = {
          apartmentId: parseInt(this.cookieService.get('apartmentId')),
          statusIds: "372,373"
        }
    
        this.moveInOutService.getMoveOutByStatusIds(params).subscribe((res: any) => {
    
          this.moveOutDataList = res.filter(item => {
            return item.isActive;
          });
    
          this.gridSourceData = {
            localdata: this.moveOutDataList,
            datatype: "array"
          }
      
          this.listData = new jqx.dataAdapter(this.gridSourceData);
          this.totalItems = this.moveOutDataList.length;
          this.isDataLoaded = true;
        });
      }
      });
  
   
  }


  @HostListener('window:checkOutUser', ['$event.detail'])
  checkOutUser(detail) {
    
    const message = `Do you want to check Out?`;

    const dialogData = new ConfirmDialogModel("Confirm Action", message);

    const dialogRef = this.dialog.open(CommonConfirmModalComponent, {
      panelClass: 'material',
      disableClose: true,
      data: dialogData
    });

    dialogRef.afterClosed().subscribe(dialogResult => {
      if (dialogResult) {
        let data = this.datagrid.getrowdata(detail.rowId);
        var moveOutData = data;
       
           let details = {
           "id": moveOutData.id,
           "outDate": moveOutData.outDate,
           "outTime": moveOutData.outTime,
           "comments": moveOutData.comments,
           "isActive": moveOutData.isActive,
           "familyCount": moveOutData.familyCount,
           "apartmentBlockUnitId": moveOutData.apartmentBlockUnitId,
           "reqUserId": moveOutData.reqUserId,
           "statusId": 376,
           "noDue": moveOutData.noDue,
           "approvedBy": parseInt(this.cookieService.get('userId')),
           "approvedDate": new Date().toISOString(),
           "insertedBy": moveOutData.insertedBy,
           "insertedOn": moveOutData.insertedOn,
           "updatedBy": parseInt(this.cookieService.get('userId')),
           "updatedOn": new Date().toISOString(),
           "apartmentId": moveOutData.apartmentId,
           "userName": moveOutData.userName,
           "mobile": moveOutData.mobile,
           "email": moveOutData.email,
           "block_Unit": moveOutData.block_Unit,
           "requestType": moveOutData.requestType,
           "statusName": 'MovedOut',
           "serialNo": moveOutData.serialNo
          }
        let params = {
         moveOut: details
        }
        this.moveInOutService.updateMoveOut(params).subscribe((res:any) => {
   
           if(res.message){
             
             this.sharedService.setAlertMessage("Moved Out successfully");
             this.isDataLoaded = false;
             let params = {
               apartmentId: parseInt(this.cookieService.get('apartmentId')),
               statusIds: "372,373"
             }
           
               this.moveInOutService.getMoveOutByStatusIds(params).subscribe((res:any) => {
           
                 this.moveOutDataList = res.filter(item => {
                   return item.isActive
                 });
           
                 console.log(this.moveOutDataList);
                 this.gridSourceData = {
                   localdata: this.moveOutDataList,
                   datatype: "array"
                 }
             
                 this.listData = new jqx.dataAdapter(this.gridSourceData);
           
                 this.totalItems = this.moveOutDataList.length;
             
                 this.isDataLoaded = true;
           
               });
           }
           else {
             this.isDataLoaded = true;
             this.isError = true;
             this.alertMessage = res.errorMessage;
           }
   
       },
       error => {
         this.isMoveOutSubmitted = true;
       });
      }
    });



  }


  @HostListener('window:approveMoveOut', ['$event.detail'])
  approveMoveOut(detail){
    let data = this.datagrid.getrowdata(detail.rowId);
    let type =  detail.column;
     var moveOutData = data;
     console.log('moveOutData', moveOutData, moment(moveOutData.outTime).format('HH:mm:ss'))
        let details = {
        "id": moveOutData.id,
        "outDate": moveOutData.outDate,
        "outTime": moveOutData.outTime,
        "comments": moveOutData.comments,
        "isActive": moveOutData.isActive,
        "familyCount": moveOutData.familyCount,
        "apartmentBlockUnitId": moveOutData.apartmentBlockUnitId,
        "reqUserId": moveOutData.reqUserId,
        "statusId": type == 'approve' ? 373 : 374,
        "noDue": moveOutData.noDue,
        "approvedBy": type == 'approve' ? parseInt(this.cookieService.get('userId')) : moveOutData.approvedBy,
        "approvedDate": type == 'approve' ? new Date().toISOString() : moveOutData.approvedDate,
        "insertedBy": moveOutData.insertedBy,
        "insertedOn": moveOutData.insertedOn,
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString(),
        "apartmentId": moveOutData.apartmentId,
        "userName": moveOutData.userName,
        "mobile": moveOutData.mobile,
        "email": moveOutData.email,
        "block_Unit": moveOutData.block_Unit,
        "requestType": moveOutData.requestType,
        "statusName": type == 'approve' ? 'Approved' : 'Rejected',
        "serialNo": moveOutData.serialNo
       }
     let params = {
      moveOut: details
     }
     this.moveInOutService.updateMoveOut(params).subscribe((res:any) => {

        if(res.message){
          let msg = type == 'approve' ? 'MoveOut approved successfully' : 'MoveOut Request Rejected'
          this.sharedService.setAlertMessage(msg);
          this.isDataLoaded = false;
          let params = {
            apartmentId: parseInt(this.cookieService.get('apartmentId')),
            statusIds: "372,373"
          }
        
            this.moveInOutService.getMoveOutByStatusIds(params).subscribe((res:any) => {
        
              this.moveOutDataList = res.filter(item => {
                return item.isActive
              });
        
              console.log(this.moveOutDataList);
              this.gridSourceData = {
                localdata: this.moveOutDataList,
                datatype: "array"
              }
          
              this.listData = new jqx.dataAdapter(this.gridSourceData);
        
              this.totalItems = this.moveOutDataList.length;
          
              this.isDataLoaded = true;
        
            });
        }
        else {
          this.isDataLoaded = true;
          this.isError = true;
          this.alertMessage = res.errorMessage;
        }

    },
    error => {
      this.isMoveOutSubmitted = true;
    });


   }

   onGlSearchFilter() {

    if(this.moveOutData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.moveOutData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }


  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
         text: 'Id',
         datafield: 'serialNo',
         width: 100,
         pinned: true,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
      }, {
          text: 'Name',
          datafield: 'userName',
          width: 100,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
      },{
         text: 'Tower Unit',
         datafield: 'block_Unit',
         cellsrenderer: cellsrenderer,
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Phone',
         datafield: 'mobile',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Email',
         datafield: 'email',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Date',
         datafield: 'outDate',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD/MM/YYYY") +'</div>'
        },
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Time',
         datafield: 'outTime',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ this.getTime(value) +'</div>'
        },
         minwidth: 170,
         renderer: columnrenderer
      }, {
        text: 'Status',
        datafield: 'statusId',
        cellsalign: 'center', 
        align: 'center',
        width: 120,
        cellsrenderer: (row, column, value): string => {
          if(value == 372) { return '<div class="dots-mr"><span class="dots bg-red-900"></span></div>'}
          else if(value == 373) { return '<div class="dots-mr"><span class="dots bg-green-900"></span></div>'}
        },
        renderer: columnrenderer
    }, {
          text: 'Actions',
          cellsalign: 'center', 
          align: 'center',
          minwidth: 120,
          cellclassname: 'action-cell',
          cellsrenderer: (row, column, value): string => {
          let id_ = this.moveOutDataList[row].statusId;
          if(id_ == 372){
          return '<div class="simple-actions">'
           +'<a href="javascript:void(0)" role="button" data-toggle="dropdown" id="moveOutDropDown" aria-haspopup="true" aria-expanded="false">'
           +'<span class="action-dot"></span>'
           +'<span class="action-dot"></span>'
           +'<span class="action-dot"></span>'
         +'</a>'
         +'<div class="dropdown-menu action-menu dropdown-menu-right" aria-labelledby="moveOutDropDown">'
           +'<a href="javascript:void(0)" onClick="approveMoveOut('+ row +', \'approve\')">Approve</a>'
           +' <a href="javascript:void(0)" onClick="approveMoveOut('+ row +', \'reject\')" >Reject</a>'
           +'<a href="javascript:void(0)" onClick="showMoveOutDetails('+ row +', \'edit\')">Edit</a>'
         +'</div>'
           +'</div>'
          }else if(id_ == 373){
            return '<div class="simple-actions">'
            +'<a href="javascript:void(0)" role="button" data-toggle="dropdown" id="moveOutDropDown" aria-haspopup="true" aria-expanded="false">'
            +'<span class="action-dot"></span>'
            +'<span class="action-dot"></span>'
            +'<span class="action-dot"></span>'
          +'</a>'
          +'<div class="dropdown-menu action-menu dropdown-menu-right" aria-labelledby="moveOutDropDown">'
          +'<a href="javascript:void(0)" class="no-link grey">Approved</a>'
            +'<a href="javascript:void(0)" onClick="showMoveOutDetails('+ row +', \'view\')">View</a>'
          +'</div>'
            +'</div>'
          }else if(id_ == 374){
            return '<div class="simple-actions">'
           +'<a href="javascript:void(0)" role="button" data-toggle="dropdown" id="moveInDropDown" aria-haspopup="true" aria-expanded="false">'
           +'<span class="action-dot"></span>'
           +'<span class="action-dot"></span>'
           +'<span class="action-dot"></span>'
           +'</a>'
           +'<div class="dropdown-menu action-menu dropdown-menu-right" aria-labelledby="moveInDropDown">'
           
           
            +'<a href="javascript:void(0)" class="no-link grey" *ngIf="id_ == 374">Rejected</a>'
           +'<a href="javascript:void(0)" onClick="showMoveOutDetails('+ row +', \'view\')">View</a>'
           +'</div>'
           +'</div>'
           }
         
     },
      renderer: columnrenderer
  }, {
    text: 'Check-Out',
    cellsalign: 'center', 
    align: 'center',
    width: 120,
    cellsrenderer: (row, column, value): string => {
      
      let id_ = this.moveOutDataList[row].statusId;
      if(id_ == 373){
         return'<div class="icon-wrapper dots-mr"  onClick="checkOutUser('+ row +')">'
         +'<img src="assets/images/checkout-icon.svg" class="svg" width="17" height="17" alt="Check Out">'
       +'</div>'
      }
   },
    renderer: columnrenderer
}];

  let params = {
    apartmentId: parseInt(this.cookieService.get('apartmentId')),
    statusIds: "372,373"
  }

    this.moveInOutService.getMoveOutByStatusIds(params).subscribe((res:any) => {

      this.moveOutDataList = res.filter(item => {
        return item.isActive
      });

      console.log(this.moveOutDataList);
      this.gridSourceData = {
        localdata: this.moveOutDataList,
        datatype: "array"
      }
  
      this.listData = new jqx.dataAdapter(this.gridSourceData);

      this.totalItems = this.moveOutDataList.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isDataLoaded = true;

    });

    this.apartmentService.getAllApartmentBlockUnits().subscribe((res:any) => {

      this.blockUnitData = res;

    });

  }

  @HostListener('window:navigateTo', ['$event.detail'])
  navigateTo(detail) {

    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let id = dataRecord.id
    this.router.navigateByUrl('/ams/moveinout-tracker/edit-moveout/' + id);

  }

}
function navigateTo(row){
  var event = new CustomEvent('navigateTo', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).navigateTo = navigateTo

function approveMoveOut(row, type){
  var event = new CustomEvent('approveMoveOut', {
    detail: {
        rowId: row,
        column: type
    }
  })
  window.dispatchEvent(event);
}
(window as any).approveMoveOut = approveMoveOut

function checkOutUser(row){

  var event = new CustomEvent('checkOutUser', {
    detail: {
        rowId: row,
            }
  })
  window.dispatchEvent(event);
}
(window as any).checkOutUser = checkOutUser;

function showMoveOutDetails(row, type){

  var event = new CustomEvent('showMoveOutDetails', {
    detail: {
        rowId: row,
        column: type
            }
  })
  window.dispatchEvent(event);
}
(window as any).showMoveOutDetails = showMoveOutDetails;