import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { MoveinoutTrackerComponent } from './moveinout-tracker.component';
import { MoveinSetupComponent } from './components/movein-setup/movein-setup.component';
import { MoveinMaintainComponent } from './components/movein-maintain/movein-maintain.component';
import { MoveoutMaintainComponent } from './components/moveout-maintain/moveout-maintain.component';
import { MoveinReportsComponent } from './components/movein-reports/movein-reports.component';
import { UserMoveinComponent } from 'src/app/shared/components/user-movein/user-movein.component';
import { UserMoveoutComponent } from 'src/app/shared/components/user-moveout/user-moveout.component';
import { MoveinMoveoutCancelComponent } from './components/movein-moveout-cancel/movein-moveout-cancel.component';
import { MoveinAdminHistoryComponent } from './components/movein-admin-history/movein-admin-history.component';
import { CreateMoveinComponent } from './components/create-movein/create-movein.component';

const routes: Routes = [
	
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: MoveinSetupComponent},
	{ path: 'movein', component: MoveinMaintainComponent},
	{ path: 'edit-movein/:id', component: UserMoveinComponent},
	{ path: 'moveout', component: MoveoutMaintainComponent},
	{ path: 'edit-moveout/:id', component: UserMoveoutComponent},
	{ path: 'reports', component: MoveinReportsComponent },
	{ path: 'cancelled', component: MoveinMoveoutCancelComponent },
	{ path: 'create-movein', component: CreateMoveinComponent },
	{ path: 'create-moveout', component: UserMoveoutComponent },
	{ path: 'movein-history', component: MoveinAdminHistoryComponent },
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MoveinoutTrackerRoutingModule { }
