import { Component, OnInit, Injector, ViewChild, HostListener } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MoveInOutService } from 'src/app/api/controllers/MoveInOut';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';
import * as _ from 'underscore';
import * as moment from 'moment';
import { Router } from '@angular/router';
import { ConfirmDialogModel, CommonConfirmModalComponent } from 'src/app/shared/components/common-confirm-modal/common-confirm-modal.component';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { ViewMoveinDetailComponent } from 'src/app/shared/components/view-movein-detail/view-movein-detail.component';

@Component({
  selector: 'app-movein-maintain',
  templateUrl: './movein-maintain.component.html',
  styleUrls: ['./movein-maintain.component.scss']
})
export class MoveinMaintainComponent implements OnInit {

  isDataLoaded: boolean = false;

  moveInData: string = "";
  moveInDataList: any;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 10;

  unitFieldType: string = "unitno";
  unitOrder: boolean = false;

  isMobile: boolean = false;

  apartmentBlockId: any;
  apartmentBlockUnitId: any;

  blockUnitData: any;

  modalService: any;


  isError: boolean = false;
  alertMessage: string = "";
  selectedInput: string = "";
  columnField: any = {};
  statusNames: any;
  statusType: any = {};
  listData: any;
  columnData:any;
  gridSourceData:any;
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;

  constructor(
    private injector: Injector,
    private moveInOutService: MoveInOutService,
    private apartmentService: ApartmentService,
    private lookupService: LookupService,
    private sharedService: SharedService,
    private cookieService: CookieService,
    private dialog: MatDialog,
    private router: Router
  ) {
    this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event) {
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type) {
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if (this.unitFieldType == type) {
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value) {
    this.selectedInput = value;
  }

  onDateChange(event, type) {

    if (event != null) {
      this.selectedInput = type;
      this.columnField[type] = moment(event).format("DD/MM/YYYY");
    }
    else {
      this.columnField = {};
    }

  }

  getDate(movein, date) {

    this.moveInDataList.map(obj => {
      if (obj.id == movein.id) {
        obj.inDateData = moment(date).format("DD/MM/YYYY");
      }
    });

    return moment(date).format("DD/MM/YYYY");
  }

  getTime(time) {
    var timeData = moment(time, 'HH:mm:ss').toDate();
    return moment(timeData).format('HH:mm A');
  }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'moveInMaintainData');
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable() {
    return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable() {
    return this.totalItems == 0 ? true : false;
  }


  getBlockNo(item, id) {
    var data = _.filter(this.blockUnitData, item => {
      if (item.apartmentBlockUnitId === id)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {

      this.moveInDataList.map(obj => {
        if (obj.id == item.id) {
          obj.apartmentBlockNumber = data[0].apartmentBlockNumber;
        }
      });

      return data[0].apartmentBlockNumber;
    }
  }

  getBlockUnitNo(item, id) {
    var data = _.filter(this.blockUnitData, item => {
      if (item.apartmentBlockUnitId === id)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {

      this.moveInDataList.map(obj => {
        if (obj.id == item.id) {
          obj.apartmentBlockUnitNumber = data[0].apartmentBlockUnitNumber;
        }
      });

      return data[0].apartmentBlockUnitNumber;
    }
  }


  getApproveStatus(id) {
    if (this.statusType[id] && id == 372) {
      return 'high';
    }
    else if (this.statusType[id] && id == 373) {
      return 'low';
    }
    else if (this.statusType[id] && id == 374) {
      return 'm-yellow';
    }
    else if (this.statusType[id] && id == 380) {
      return 'grey-850';
    }

  }
  @HostListener('window:approveMoveIn', ['$event.detail'])
  approveMoveIn(detail) {
    let data = this.datagrid.getrowdata(detail.rowId);
    let type =  detail.column;
    let details = {
      "id": data.id,
      "inDate": data.inDate,
      "inTime": data.inTime,
      "familyCount": data.familyCount,
      "comments": data.comments,
      "isActive": data.isActive,
      "apartmentBlockUnitId": data.apartmentBlockUnitId,
      "reqUserId": data.reqUserId,
      "statusId": type == 'approve' ? 373 : 374,
      "approvedBy": type == 'approve' ? parseInt(this.cookieService.get('userId')) : data.approvedBy,
      "approvedDate": type == 'approve' ? new Date().toISOString() : data.approvedDate,
      "insertedBy": data.insertedBy,
      "insertedOn": data.insertedOn,
      "updatedBy": parseInt(this.cookieService.get('userId')),
      "updatedOn": new Date().toISOString(),
      "apartmentId": data.apartmentId,
      "userName": data.userName,
      "mobile": data.mobile,
      "email": data.email,
      "block_Unit": data.block_Unit,
      "requestType": data.requestType,
      "statusName": type == 'approve' ? "Approved" : "Rejected",
      "serialNo": data.serialNo
    }

    let params = {
      moveIn: details
    }

    this.isDataLoaded = false;

    this.moveInOutService.updateMoveIn(params).subscribe((res: any) => {

      if (res.message) {
        let msg = type == 'approve' ? 'Movein approved successfully' : 'Movein Request Rejected'
          this.sharedService.setAlertMessage(msg);
          let params = {
            apartmentId: parseInt(this.cookieService.get('apartmentId')),
            statusIds: "372,373"
          }
      
          this.moveInOutService.getMoveInByStatusIds(params).subscribe((res: any) => {
      
            this.moveInDataList = res.filter(item => {
              return item.isActive;
            });
      
            this.gridSourceData = {
              localdata: this.moveInDataList,
              datatype: "array"
            }
        
            this.listData = new jqx.dataAdapter(this.gridSourceData);
            this.totalItems = this.moveInDataList.length;
            this.isDataLoaded = true;
          });
               
       
      }
      else {
        this.isError = true;
        this.alertMessage = res.errorMessage;
      }

    },
      error => {
        
      });


  }

  onGlSearchFilter() {

    if(this.moveInData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.moveInData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  ngOnInit() {

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
         text: 'Id',
         datafield: 'serialNo',
         width: 100,
         pinned: true,
         cellsrenderer: cellsrenderer,
         renderer: columnrenderer
      }, {
          text: 'Name',
          datafield: 'userName',
          width: 100,
          cellsrenderer: cellsrenderer,
          renderer: columnrenderer
      },{
         text: 'Unit Details',
         datafield: 'block_Unit',
         cellsrenderer: cellsrenderer,
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Phone',
         datafield: 'mobile',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Email',
         datafield: 'email',
         cellsrenderer: cellsrenderer,
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Expected Date',
         datafield: 'inDate',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD/MM/YYYY") +'</div>'
        },
         minwidth: 80,
         renderer: columnrenderer
     }, {
         text: 'Expected Time',
         datafield: 'inTime',
         cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ this.getTime(value) +'</div>'
        },
         minwidth: 170,
         renderer: columnrenderer
     }, {
         text: 'Status',
         datafield: 'statusId',
         cellsalign: 'center', 
         align: 'center',
         width: 120, 
         cellsrenderer: (row, column, value): string => {
           if(value == 372) { return '<div class="dots-mr"><span class="dots bg-red-900 dots-mr"></span></div>'}
           else if(value == 373) { return '<div class="dots-mr"><span class="dots bg-green-900 dots-mr"></span></div>'}
          },
         renderer: columnrenderer
     }, {
          text: 'Actions',
          cellsalign: 'center', 
          align: 'center',
          minwidth: 120,
          cellclassname: 'action-cell',
          cellsrenderer: (row, column, value): string => {
          let id_ = this.moveInDataList[row].statusId;
          if(id_ == 372){
           return '<div class="simple-actions">'
           +'<a href="javascript:void(0)" role="button" data-toggle="dropdown" id="moveInDropDown" aria-haspopup="true" aria-expanded="false">'
           +'<span class="action-dot"></span>'
           +'<span class="action-dot"></span>'
           +'<span class="action-dot"></span>'
           +'</a>'
           +'<div class="dropdown-menu action-menu dropdown-menu-right" aria-labelledby="moveInDropDown">'
           
           +'<a href="javascript:void(0)" onClick="approveMoveIn('+ row +', \'approve\')" >Approve</a>'
           +' <a href="javascript:void(0)" onClick="approveMoveIn('+ row +', \'reject\')" >Reject</a>'
           +'<a href="javascript:void(0)" onClick="showMoveInDetails('+ row +', \'edit\')">Edit</a>'
           +'</div>'
           +'</div>'}
           else if(id_ == 373){
            return '<div class="simple-actions">'
            +'<a href="javascript:void(0)" role="button" data-toggle="dropdown" id="moveInDropDown" aria-haspopup="true" aria-expanded="false">'
            +'<span class="action-dot"></span>'
            +'<span class="action-dot"></span>'
            +'<span class="action-dot"></span>'
            +'</a>'
            +'<div class="dropdown-menu action-menu dropdown-menu-right" aria-labelledby="moveInDropDown">'
            
           
            +' <a  href="javascript:void(0)" class="no-link grey" *ngIf="id_ == 373">Approved</a>'
            +'<a href="javascript:void(0)" onClick="showMoveInDetails('+ row +', \'view\')">View</a>'
            +'</div>'
            +'</div>'
           }else if(id_ == 374){
            return '<div class="simple-actions">'
           +'<a href="javascript:void(0)" role="button" data-toggle="dropdown" id="moveInDropDown" aria-haspopup="true" aria-expanded="false">'
           +'<span class="action-dot"></span>'
           +'<span class="action-dot"></span>'
           +'<span class="action-dot"></span>'
           +'</a>'
           +'<div class="dropdown-menu action-menu dropdown-menu-right" aria-labelledby="moveInDropDown">'
           
           
            +'<a href="javascript:void(0)" class="no-link grey" *ngIf="id_ == 374">Rejected</a>'
           +'<a href="javascript:void(0)" onClick="showMoveInDetails('+ row +', \'reject\')">View</a>'
           +'</div>'
           +'</div>'
           }
     },
      renderer: columnrenderer
  }, {
    text: 'Check-In',
    cellsalign: 'center', 
    align: 'center',
    width: 120,
    cellsrenderer: (row, column, value): string => {
      
      let id_ = this.moveInDataList[row].statusId;
      if(id_ == 373){
         return'<div class="icon-wrapper dots-mr"  onClick="chekInUser('+ row +')">'
         +'<img src="assets/images/checkin-icon.svg" class="svg" width="17" height="17" alt="Check In">'
       +'</div>'
      }
   },
    renderer: columnrenderer
}];


    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      statusIds: "372,373"
    }

    this.moveInOutService.getMoveInByStatusIds(params).subscribe((res: any) => {

      this.moveInDataList = res.filter(item => {
        return item.isActive;
      });

      this.gridSourceData = {
        localdata: this.moveInDataList,
        datatype: "array"
      }
  
      this.listData = new jqx.dataAdapter(this.gridSourceData);
      this.totalItems = this.moveInDataList.length;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isDataLoaded = true;

    });

    this.apartmentService.getAllApartmentBlockUnits().subscribe((res: any) => {

      this.blockUnitData = res;

    });
    this.getStatusName();
  }

  

  @HostListener('window:showMoveInDetails', ['$event.detail'])
  showMoveInDetails(detail): void {
    let details = this.datagrid.getrowdata(detail.rowId);
    let type_ =  detail.column;
     const dialogRef = this.dialog.open(ViewMoveinDetailComponent, {
        width: '400px',
        data: {
          data: details,
          type: type_
        }
      });
  
      dialogRef.afterClosed().subscribe(result => {
        
        if(result){
        this.isDataLoaded = false;
        let params = {
          apartmentId: parseInt(this.cookieService.get('apartmentId')),
          statusIds: "372,373"
        }
    
        this.moveInOutService.getMoveInByStatusIds(params).subscribe((res: any) => {
    
          this.moveInDataList = res.filter(item => {
            return item.isActive;
          });
    
          this.gridSourceData = {
            localdata: this.moveInDataList,
            datatype: "array"
          }
      
          this.listData = new jqx.dataAdapter(this.gridSourceData);
          this.totalItems = this.moveInDataList.length;
          this.isDataLoaded = true;
        });
      }
      });
  
   
  }

  @HostListener('window:chekInUser', ['$event.detail'])
  chekInUser(detail) {
    let data = this.datagrid.getrowdata(detail.rowId);
    const message = `Do you want to check In?`;

    const dialogData = new ConfirmDialogModel("Confirm Action", message);

    const dialogRef = this.dialog.open(CommonConfirmModalComponent, {
      panelClass: 'material',
      disableClose: true,
      data: dialogData
    });

    dialogRef.afterClosed().subscribe(dialogResult => {
      if (dialogResult) {
        let details = {
          "id": data.id,
          "inDate": data.inDate,
          "inTime": data.inTime,
          "familyCount": data.familyCount,
          "comments": data.comments,
          "isActive": data.isActive,
          "apartmentBlockUnitId": data.apartmentBlockUnitId,
          "reqUserId": data.reqUserId,
          "statusId": 375,
          "approvedBy": data.approvedBy,
          "approvedDate": data.approvedDate,
          "insertedBy": data.insertedBy,
          "insertedOn": data.insertedOn,
          "updatedBy": parseInt(this.cookieService.get('userId')),
          "updatedOn": new Date().toISOString(),
          "apartmentId": data.apartmentId,
          "userName": data.userName,
          "mobile": data.mobile,
          "email": data.email,
          "block_Unit": data.block_Unit,
          "requestType": data.requestType,
          "statusName": 'Movedin',
          "serialNo": data.serialNo
        }
    
        let params = {
          moveIn: details
        }
    
        this.isDataLoaded = false;
        this.moveInOutService.updateMoveIn(params).subscribe((res: any) => {
          if (res.message) {
           
            this.sharedService.setAlertMessage("Checked In successfully");
            let params = {
              apartmentId: parseInt(this.cookieService.get('apartmentId')),
              statusIds: "372,373"
            }
        
            this.moveInOutService.getMoveInByStatusIds(params).subscribe((res: any) => {
        
              this.moveInDataList = res.filter(item => {
                return item.isActive;
              });
        
              this.gridSourceData = {
                localdata: this.moveInDataList,
                datatype: "array"
              }
          
              this.listData = new jqx.dataAdapter(this.gridSourceData);
              this.totalItems = this.moveInDataList.length;
              this.isDataLoaded = true;
            });
                 
          }
          else {
            this.isError = true;
            this.isDataLoaded = true;
            this.alertMessage = res.errorMessage;
          }

        },
          error => {
            this.isDataLoaded = true;
          });
      }
    });



  }

  getStatusName() {

    let params = {
      ApartmentId: parseInt(this.cookieService.get('apartmentId')),
      LookupTypeId: 92
    }

    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res: any) => {

      this.statusNames = res.filter(item => {
        return item.isActive;
      });
      this.statusNames.forEach(element => {
        this.statusType[element.lookupValueId] = element;
      });
    });
  }

  navigateToMovein() {
    this.router.navigate(['ams/moveinout-tracker/create-movein']);
  }

}


function approveMoveIn(row, type){

  var event = new CustomEvent('approveMoveIn', {
    detail: {
        rowId: row,
        column: type
    }
  })
  window.dispatchEvent(event);
}
(window as any).approveMoveIn = approveMoveIn;

function showMoveInDetails(row, type){

  var event = new CustomEvent('showMoveInDetails', {
    detail: {
        rowId: row,
        column: type
            }
  })
  window.dispatchEvent(event);
}
(window as any).showMoveInDetails = showMoveInDetails;

function chekInUser(row){

  var event = new CustomEvent('chekInUser', {
    detail: {
        rowId: row,
            }
  })
  window.dispatchEvent(event);
}
(window as any).chekInUser = chekInUser;

