import { Component, OnInit } from '@angular/core';

import { MoveInOutService } from 'src/app/api/controllers/MoveInOut';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import { MatDialog } from '@angular/material/dialog';
import { AddDocCriteriaComponent } from 'src/app/shared/components/add-doc-criteria/add-doc-criteria.component';
import { ConfirmModalComponent } from 'src/app/shared/components/confirm-modal/confirm-modal.component';

@Component({
  selector: 'app-movein-setup',
  templateUrl: './movein-setup.component.html',
  styleUrls: ['./movein-setup.component.scss']
})
export class MoveinSetupComponent implements OnInit {


  isMobile: boolean = false;

  documentDataList: any;

  isDataLoaded: boolean = false;

  documentTypeDataList: any;
  documentTypeListObject: any = {};

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any;
  itemLimit: any = 10;

  unitFieldType: string = "unitno";
  unitOrder: boolean = false;

  constructor(
    private moveInOutService: MoveInOutService,
    private lookupService: LookupService,
    private sharedService: SharedService,
    private cookieService: CookieService,
    private dialog: MatDialog
  ) { }

  getIndexParams(event) {
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type) {
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  getStatus(value) {
    if (value) {
      return 'YES'
    }
    else {
      return 'NO'
    }
  }

  getCategory(Id: number) {
    if (this.documentTypeListObject[Id]) {
      return this.documentTypeListObject[Id].lookupValueName;
    } else {
      return '';
    }
  }

  getDocumentType(priorityId) {

    var data = _.filter(this.documentTypeDataList, function (item) {
      if (item.lookupValueId === priorityId)
        return item;
    });

    if (data === undefined || data.length == 0) {
      return '';
    }
    else {
      return data[0].lookupValueName == 'General' ? 'low' : 'medium';
    }

  }

  updateDocType(document) {


    setTimeout(() => {

      this.isDataLoaded = false;

      var details = {

        "id": parseInt(document.id),
        "description": document.description,
        "documentTypeId": parseInt(document.documentTypeId),
        "owner": document.owner,
        "tenant": document.tenant,
        "isMoveIn": document.isMoveIn,
        "comments": document.comments,
        "isMandate": document.isMandate,
        "isActive": document.isActive,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString(),
        "apartmentId": parseInt(this.cookieService.get('apartmentId'))

      }

      let params = {
        moveInOutConfig: details
      }

      this.moveInOutService.updateMoveInOutConfig(params).subscribe((res: any) => {

        if (res.message) {

          this.isDataLoaded = true;
          this.sharedService.setAlertMessage("MoveIn Configuration updated successfully");

        }
        else {
          this.isDataLoaded = true;
        }


      },
        error => {
          this.isDataLoaded = true;
        });

    }, 1000);

  }

  ngOnInit() {

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.moveInOutService.getMoveInOutConfigByApartmentId(params).subscribe((res: any) => {

      this.documentDataList = res.filter(item => {
        return item.isActive;
      });

      this.totalItems = this.documentDataList.length;

      if (this.totalItems > this.itemLimit) {
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.isDataLoaded = true;


    }, error => {
      console.log(error);
    });

    //document type

    let docListparams = {
      LookupTypeId: 72
    }

    this.lookupService.getLookupValueByLookupTypeId(docListparams).subscribe((res: any) => {

      this.documentTypeDataList = res.filter(item => {
        return item.isActive;
      });

      this.documentTypeDataList.forEach(element => {
        this.documentTypeListObject[element.lookupValueId] = element;
      });

    },
      error => {

      }
    );

  }

  openDocCriteria(): void {
    const dialogRef = this.dialog.open(AddDocCriteriaComponent, {
      width: '400px',
      data: {
        type: 'ADD'
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      if(result.details){
        let document = result.details;
       this.isDataLoaded = false;
 
       var details = {
 
         "description": document.description,
         "documentTypeId": parseInt(document.documentTypeId),
         "owner": document.owner,
         "tenant": document.tenant,
         "isMoveIn": document.isMoveIn,
         "comments": document.comments,
         "isMandate": document.isMandate,
         "isActive": document.isActive,
         "isForeignNational": document.isForeignNational,
         "insertedBy": parseInt(this.cookieService.get('userId')),
         "insertedOn": new Date().toISOString(),
         "updatedBy": null,
         "updatedOn": null,
         "apartmentId": parseInt(this.cookieService.get('apartmentId'))
 
       }
 
       let params = {
         moveInOutConfig: details
       }
 
       this.moveInOutService.addMoveInOutConfig(params).subscribe((res: any) => {
 
         if (res.message) {
 
           
           this.sharedService.setAlertMessage("Document Criteria Added successfully");
           let params = {
             apartmentId: parseInt(this.cookieService.get('apartmentId'))
           }
       
           this.moveInOutService.getMoveInOutConfigByApartmentId(params).subscribe((res: any) => {
       
             this.documentDataList = res.filter(item => {
               return item.isActive;
             });
             this.isDataLoaded = true;
             },
         error => {
           this.isDataLoaded = true;
         });
       }
     })
      }
    });
  }

  openDocCriteriaEdit(docData): void {
    const dialogRef = this.dialog.open(AddDocCriteriaComponent, {
      width: '400px',
      data: {
        type: 'EDIT',
        data: docData
      }
    });

    dialogRef.afterClosed().subscribe(result => {
     if(result.details){
       let document = result.details;
      this.isDataLoaded = false;

      var details = {

        "id": parseInt(document.id),
        "description": document.description,
        "documentTypeId": parseInt(document.documentTypeId),
        "owner": document.owner,
        "tenant": document.tenant,
        "isMoveIn": document.isMoveIn,
        "comments": document.comments,
        "isMandate": document.isMandate,
        "isActive": document.isActive,
        "isForeignNational": document.isForeignNational,
        "insertedBy": document.insertedBy,
        "insertedOn": document.insertedOn,
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString(),
        "apartmentId": parseInt(this.cookieService.get('apartmentId'))

      }

      let params = {
        moveInOutConfig: details
      }

      this.moveInOutService.updateMoveInOutConfig(params).subscribe((res: any) => {

        if (res.message) {

          
          this.sharedService.setAlertMessage("Document Criteria updated successfully");
          let params = {
            apartmentId: parseInt(this.cookieService.get('apartmentId'))
          }
      
          this.moveInOutService.getMoveInOutConfigByApartmentId(params).subscribe((res: any) => {
      
            this.documentDataList = res.filter(item => {
              return item.isActive;
            });
            this.isDataLoaded = true;
            },
        error => {
          this.isDataLoaded = true;
        });
      }
    })
     }
  })
}

  openDeleteConfirm(docId) {
    const dialogRef = this.dialog.open(ConfirmModalComponent, {
      width: '400px',
      id: docId
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result){
      this.isDataLoaded = false;
      
      let params = {
        Id: parseInt(docId),
        deleteBy: parseInt(this.cookieService.get('userId'))
      }
  
      this.moveInOutService.deleteMoveInOutConfig(params).subscribe((res: any) => {
         
       if(res.message) {
        let params = {
          apartmentId: parseInt(this.cookieService.get('apartmentId'))
        }
    
        this.moveInOutService.getMoveInOutConfigByApartmentId(params).subscribe((res: any) => {
    
          this.documentDataList = res.filter(item => {
            return item.isActive;
          });
    
          this.totalItems = this.documentDataList.length;
    
          if (this.totalItems > this.itemLimit) {
            this.ItemEndIndex = this.itemLimit;
          }
          else {
            this.ItemEndIndex = this.totalItems;
          }
  
          this.isDataLoaded = true;
    
    
        }, error => {
          console.log(error);
        });
    
       }
  
  
      }, error => {
        console.log(error);
        this.isDataLoaded = true;
      });
    }
    });
  }

}
